import React from 'react';
import Skeleton from '@mui/material/Skeleton';
import Stack from '@mui/material/Stack';
import { Box } from '@mui/material';

export default function LoadingChats() {
  return (
    <Box sx={{ height: "20rem", width: "17rem", padding: 0, borderRadius: "8px"}}>
      {/* For variant="text", adjust the height via font-size */}
      {/* <Skeleton variant="" sx={{ fontSize: '1rem' }} /> */}
      {/* For other variants, adjust the size with `width` and `height` */}
      {/* <Skeleton variant="circular" width={40} height={40} /> */}
      {/* <Skeleton variant="rectangular" width={210} height={60} /> */}
      <Skeleton variant="rounded" animation="wave" sx={{background:"#5a5a7257",borderRadius:"15px",height: "20rem", width: "17rem"}} />
    </Box>
  );
}
